import React, { useMemo } from 'react';
import { FormContext } from './formContext';
import { FormProps } from './types';

export const DEFAULT_PROPS = {
  labelWidth: 150,
  labelAside: true,
  size: 'default'
};

export const Form: React.FC<FormProps> = ({
  children,
  labelWidth,
  labelAside,
  size
}) => {
  const value = useMemo(
    () => ({ labelWidthForm: labelWidth, labelAsideForm: labelAside, sizeForm: size }),
    [labelAside, labelWidth, size]
  );

  return (
    <FormContext.Provider value={ value }>
      { children }
    </FormContext.Provider>
  );
};

Form.defaultProps = DEFAULT_PROPS as FormProps;
